

































import { Component, Vue, Prop } from 'vue-property-decorator'

@Component({})
export default class PromotionListItem extends Vue {
  private totalAsset?: string = this.numberWithCommas(
    this.$store.state.userBalance
  )
  private walletBalance?: string = '0.00'
  private popUpStyle?: object = {
    height: 'auto',
    width: '173px',
    borderRadius: '5px',
    top: '84px',
    boxShadow: '0 0 1px #888',
    // left: '73%',
  }
  @Prop({ type: Boolean, default: false }) public isShow!: boolean
  private getContainer() {
    return document.querySelector('.user-balance')
  }
  private goToLink() {
    this.$router.push({ name: 'Recharge' })
  }

  private numberWithCommas(x: String) {
    return x
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ',')
      .concat('.00')
  }
}
